import React, { useRef, useEffect } from 'react';
import {
  Container,
  Grid,
  Typography,
  Box,
  Button,
  Card,
  CardMedia,
  CardContent,
} from '@mui/material';
import { motion, useScroll, useTransform } from 'framer-motion';
import { useNavigate } from 'react-router-dom';

import floatProduct from '../assets/float-product.png';
import img1 from '../assets/images/whatsapp-image-2025-06-14.jpg';
import img2 from '../assets/images/screenshot-2025-06-14-072257.png';
import img3 from '../assets/images/screenshot-2025-06-14-072340.png';

const Home = () => {
  const navigate = useNavigate();
  const { scrollYProgress } = useScroll();
  const scale = useTransform(scrollYProgress, [0, 1], [1, 0.8]);
  const opacity = useTransform(scrollYProgress, [0, 1], [1, 0.5]);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.style.opacity = '1';
            entry.target.style.transform = 'translateY(0)';
          }
        });
      },
      { threshold: 0.1 }
    );

    const elements = document.querySelectorAll('.fade-in');
    elements.forEach((element) => observer.observe(element));

    return () => {
      elements.forEach((element) => observer.unobserve(element));
    };
  }, []);

  return (
    <Box>

      {/* Hero Image Section */}
      <Box
        sx={{
          position: 'relative',
          width: '100%',
          height: { xs: 400, sm: 500, md: 600 },
          overflow: 'hidden',
        }}
      >
        <CardMedia
          component="img"
          image={img1}
          alt="Hero Float"
          sx={{
            width: '100%',
            height: '100%',
            objectFit: 'cover',
            filter: 'brightness(0.6)',
          }}
        />
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            textAlign: 'center',
            color: 'white',
          }}
        >
          <Typography variant="h2" sx={{ fontWeight: 'bold', mb: 2 }}>
            Welcome to AquaChilly
          </Typography>
          <Typography variant="h5">
            Discover our premium float products designed for ultimate comfort and style
          </Typography>
        </Box>
      </Box>

      {/* Premium Float Experience Section */}
      <Container maxWidth="lg" sx={{ mt: 8 }}>
        <Grid container spacing={4} alignItems="center">
          <Grid item xs={12} md={6}>
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 1 }}
            >
              <Card
                sx={{
                  height: '100%',
                  overflow: 'hidden',
                  position: 'relative',
                  borderRadius: 3,
                }}
              >
                <motion.div style={{ scale, opacity }}>
                  <CardMedia
                    component="img"
                    height="400"
                    image={floatProduct}
                    alt="Premium Float"
                    sx={{ objectFit: 'cover' }}
                  />
                </motion.div>
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: scrollYProgress }}
                  style={{
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    right: 0,
                    bottom: 0,
                    background: 'linear-gradient(transparent, rgba(0, 151, 178, 0.6))',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                  }}
                >
                  <Typography
                    variant="h3"
                    sx={{
                      color: 'white',
                      fontWeight: 'bold',
                      textShadow: '2px 2px 6px rgba(0,0,0,0.4)',
                    }}
                  >
                    Float in Style
                  </Typography>
                </motion.div>
              </Card>
            </motion.div>
          </Grid>

          <Grid item xs={12} md={6}>
            <CardContent>
              <Typography variant="h3" gutterBottom>
                Premium Float Experience
              </Typography>
              <Typography variant="body1" paragraph>
                Experience the ultimate comfort with our premium float products. Designed for maximum relaxation and durability.
              </Typography>
              <Typography variant="body1" paragraph>
                Perfect for any water activity  from pool relaxation to ocean adventures.
              </Typography>
              <Button
                variant="contained"
                size="large"
                onClick={() => navigate('/products')}
                sx={{
                  mt: 2,
                  px: 4,
                  py: 1.5,
                  borderRadius: 2,
                  textTransform: 'none',
                  fontWeight: 'bold',
                  backgroundColor: '#0097B2',
                  boxShadow: '0 4px 12px rgba(0, 151, 178, 0.3)',
                  '&:hover': {
                    backgroundColor: '#007B94',
                    transform: 'translateY(-2px)',
                  },
                }}
              >
                Shop Now
              </Button>
            </CardContent>
          </Grid>
        </Grid>
      </Container>

      {/* Two Images at Bottom */}
      <Container maxWidth="lg" sx={{ mt: 8, mb: 6 }}>
        <Grid container spacing={4}>
          {[img2, img3].map((img, i) => (
            <Grid item xs={12} md={6} key={i}>
              <motion.div
                className="fade-in"
                initial={{ opacity: 0, y: 30 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.8 + i * 0.2 }}
              >
                <Card
                  sx={{
                    borderRadius: 3,
                    overflow: 'hidden',
                    boxShadow: 4,
                    transition: 'transform 0.3s',
                    '&:hover': { transform: 'scale(1.03)' },
                  }}
                >
                  <CardMedia
                    component="img"
                    height="350"
                    image={img}
                    alt={`Float ${i + 2}`}
                    sx={{ objectFit: 'cover' }}
                  />
                </Card>
              </motion.div>
            </Grid>
          ))}
        </Grid>
      </Container>

      {/* Contact Information Card */}
      <Box sx={{ width: '100%', bgcolor: '#f5f5f5', py: 6 }}>
        <Container maxWidth="lg">
          <Card sx={{
            borderRadius: 3,
            boxShadow: 3,
            p: 4,
            backgroundColor: '#0097b2',
            color: 'white',
          }}>
            <Grid container spacing={4}>
              <Grid item xs={12} md={4}>
                <Box sx={{ textAlign: 'center' }}>
                  <Typography variant="h4" gutterBottom>
                    Contact Us
                  </Typography>
                  <Typography variant="body1" color="textSecondary">
                    Have questions? We're here to help!
                  </Typography>
                  <Box sx={{ mt: 2 }}>
                    <Typography variant="subtitle1">
                      Phone: +20 123 456 7890
                    </Typography>
                    <Typography variant="subtitle1">
                      Email: info@aqua-chilly.com
                    </Typography>
                  </Box>
                </Box>
              </Grid>
              <Grid item xs={12} md={4}>
                <Box sx={{ textAlign: 'center' }}>
                  <Typography variant="h4" gutterBottom>
                    Our Location
                  </Typography>
                  <Typography variant="body1" color="textSecondary">
                    Visit us in person
                  </Typography>
                  <Box sx={{ mt: 2 }}>
                    <Typography variant="subtitle1">
                      123 Water Street
                    </Typography>
                    <Typography variant="subtitle1">
                      Cairo, Egypt
                    </Typography>
                  </Box>
                </Box>
              </Grid>
              <Grid item xs={12} md={4}>
                <Box sx={{ textAlign: 'center' }}>
                  <Typography variant="h4" gutterBottom>
                    Opening Hours
                  </Typography>
                  <Typography variant="body1" color="textSecondary">
                    Store Hours
                  </Typography>
                  <Box sx={{ mt: 2 }}>
                    <Typography variant="subtitle1">
                      Monday - Friday: 10:00 - 18:00
                    </Typography>
                    <Typography variant="subtitle1">
                      Saturday: 10:00 - 16:00
                    </Typography>
                    <Typography variant="subtitle1">
                      Sunday: Closed
                    </Typography>
                  </Box>
                </Box>
              </Grid>
            </Grid>
          </Card>
        </Container>
      </Box>
    </Box>
  );
};

export default Home;
